import "@fontsource/righteous/latin.css"
import "@fontsource/oswald/latin-700.css"
import FontFaceObserver from "fontfaceobserver/fontfaceobserver.standalone"

const Observers = {}

export default async function FontObserver(fontName, options?): Promise<void> {
  if (!Observers[fontName]) {
    Observers[fontName] = new FontFaceObserver(fontName, options);
  }
  return Observers[fontName].load()
}
